import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { WebSocketSubject } from 'rxjs/webSocket';
import { Coordinates } from '../shared/models/coordinates';
import { DataService } from './data.service';
import mocking from '../../assets/recording_mock.json';

@Injectable({
  providedIn: 'root'
})
export class CoordinatesService {

  socket: WebSocketSubject<any>;
  lastPose?: any;
  update: BehaviorSubject<Coordinates>;
  play: BehaviorSubject<Coordinates>;
  playPosition: BehaviorSubject<number>;
  connections: { [index: string]: Array<string> } = {};

  // editor playback attributes
  recording: Array<[number, [any]]> = [];
  ri = 1;
  isPlaying = false;
  playbackSpeed = 1.0;
  timeouts: Array<number> = [];

  constructor(private dataService: DataService) {
    this.socket = dataService.getWebsocket();
    this.socket.subscribe(message => {
      this.lastPose = message;
      this.update.next(this.lastPose);
    });
    this.update = new BehaviorSubject<Coordinates>(this.lastPose!);
    this.play = new BehaviorSubject<Coordinates>(this.lastPose!);
    this.playPosition = new BehaviorSubject<number>(this.ri);
    /* this.connections = {
      OP_R_Shoulder: ['OP_R_Elbow', 'R_Hip'],
      OP_R_Elbow: ['OP_R_Wrist'],
      OP_L_Shoulder: ['OP_L_Elbow', 'L_Hip', 'OP_R_Shoulder'],
      OP_L_Elbow: ['OP_L_Wrist'],
      R_Hip: ['OP_R_Hip', 'L_Hip', 'OP_R_Knee'],
      OP_R_Hip: ['OP_L_Hip', 'OP_R_Knee'],
      OP_R_Knee: ['OP_R_Ankle'],
      OP_L_Hip: ['OP_L_Knee'],
      L_Hip: ['OP_L_Hip', 'OP_L_Knee'],
      OP_L_Knee: ['OP_L_Ankle'],
      OP_L_Ankle: ['OP_L_Heel'],
      OP_L_Heel: ['OP_L_Small_Toe', 'OP_L_Big_Toe'],
      OP_L_Small_Toe: ['OP_L_Big_Toe'],
      OP_R_Ankle: ['OP_R_Heel'],
      OP_R_Heel: ['OP_R_Big_Toe'],
      OP_R_Big_Toe: ['OP_R_Small_Toe'],
      OP_R_Small_Toe: ['OP_R_Heel'],
      Jaw_HM: ['OP_R_Ear', 'OP_L_Ear', 'OP_Nose'],
      OP_Nose: ['OP_R_Eye', 'OP_L_Eye'],
      OP_R_Eye: ['OP_L_Eye'],
      OP_R_Ear: ['Head_HM'],
      OP_L_Ear: ['Head_HM'],
      OP_Neck: ['Neck_LSP'],
      Neck_LSP: ['Head_HM'],
      Spine_HM: ['Neck_LSP', 'Pelvis_MPII']
    }; */
    this.dataService.getConnectionsDict().subscribe((val: any) => this.buildConnectionsDict(val));
    
    this.useMocking();
  }

  startRecording(): void {
    this.dataService.startRecording();
  }

  stopRecording(): void {
    this.dataService.stopRecording().subscribe(res => {
      this.recording = res;
      console.log(this.recording);
    });
  }

  buildConnectionsDict(val: any): void {
    let dict: { [index: string]: Array<string> } = {};
    const used = val.ownpose_used;
    const labels = val.ownpose_labels as Array<string>;
    const matrabs = val.ownpose as Array<[number, number]>;

    matrabs.forEach(val=> {
      if(dict[labels[val[0]]]) {
        dict[labels[val[0]]].push(labels[val[1]]);  
      } else {
        dict[labels[val[0]]] = [labels[val[1]]];  
      }
    });
    console.log(dict);
    this.connections = dict;
  }

  useMocking(): void {
    this.recording = mocking as Array<[number, [any]]>;
    this.lastPose = this.recording[0][1];
  }

  saveRecording(name: string, description: string, repetitions: number, features: any): void {
    this.dataService.saveRecording(name, description, repetitions, features, this.recording);
  }

  playingRecording(): void {
    if (this.ri === this.recording.length - 1) {
      this.ri = 1;
    }
    const time = this.recording[this.ri][0] - this.recording[this.ri - 1][0];
    if (this.recording) {
      const id = window.setTimeout(() => {

        if (this.isPlaying === true) {
          console.log(this.ri, this.recording[this.ri][1]);
          this.playPosition.next(this.ri);
          this.play.next(this.recording[this.ri][1]);
          this.ri++;
          this.playingRecording();
        }
      }, time * (1 / this.playbackSpeed));
      this.timeouts.push(id);
    }

  }

  playRecording(): void {
    this.isPlaying = true;
    this.playingRecording();
  }

  stopPlayingRecording(): void {
    this.isPlaying = false;
  }

  setPlaybackSpeed(playbackSpeed: number): void {
    this.playbackSpeed = playbackSpeed;
  }

  clearTimeouts(): void {
    this.timeouts.forEach(id => {
      window.clearTimeout(id);
    });
  }

  setPlayPosition(val: number) {
    // this.clearTimeouts();
    this.ri = val;
    this.play.next(this.recording[this.ri][1]);
    // this.playRecording();
  }

}
