import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-edk',
  templateUrl: './edk.component.html',
  styleUrls: ['./edk.component.scss']
})
export class EDKComponent implements OnInit {

  index = 0;
  exercise = null;

  constructor() { }

  ngOnInit(): void {
  }

  editExerciseFromBrowser(exercise: any) {
    this.index = 0;
    this.exercise = exercise;
  }

}
