import {AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, HostListener, Input, OnInit, ViewChild} from '@angular/core';
import * as three from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js';
import joint_dictionary from '../../../assets/skeleton_dictionary.json';
import {CoordinatesService} from '../../services/coordinates.service';
import {DataService} from '../../services/data.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';


export enum TabPosition {
  Angle = -1,
  Distance = 1,
  Height = 2
}

@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss'],
  changeDetection: ChangeDetectionStrategy.Default
})
export class EditorComponent implements OnInit, AfterViewInit {

  constructor(private dataService: DataService, protected coordinatesService: CoordinatesService, private snackBar: MatSnackBar) {
    const width = window.innerWidth;
    const height = window.innerHeight;
    this.renderer = new three.WebGLRenderer({ antialias: true });
    this.scene = new three.Scene();
    this.camera = new three.PerspectiveCamera(25, (width * 2 / 3) / height, 0.1, 1000);
    this.orbitControls = new OrbitControls(this.camera, this.renderer.domElement);
    this.orbitControls.update();
  }

  Object = Object;

  @ViewChild('editorContainer') editorContainer!: ElementRef;
  renderer: three.WebGLRenderer;
  scene: three.Scene;
  camera: three.PerspectiveCamera;
  raycaster = new three.Raycaster();


  @Input()
  get exercise(): string{return this.exName}
  set exercise(val: any){
    console.log(val);
    if(!val) {return;}
    if(val.name) {this.exName = val.name;}
    if(val.description) {this.exDescription = val.description;}
    if(val.repetitions) {this.repetitions = val.repetitions;}
    if(val.features) {
      this.features = val.features;
      this.featuresArray = Object.values(this.features);
    }
    if(val.recording) {
      this.coordinatesService.recording = val.recording;
      this.coordinatesService.isPlaying = false;
      this.coordinatesService.ri = 1;
      this.coordinatesService.lastPose = val.recording[1];
    }
    if(val._id) {
      this.id = val._id;
    }
    this.resetScene();
  }

  id: string | undefined;

  // properties
  viewHeight = window.innerHeight - 70 - 48 - 48;
  viewWidth = window.innerWidth;
  pixelRatio = window.devicePixelRatio;
  mouse = new three.Vector2();
  touch = new three.Vector2();

  // skeleton
  dots: Array<{ index: string, dot: THREE.Mesh }> = [];
  dotsMapping: Array<[string, string]> = [];
  lines: Array<THREE.Line> = [];

  // controlling
  orbitControls: OrbitControls;

  // recording Pose
  recordingPose: any;
  exName = '';
  repetitions = 1;
  exDescription = '';
  playbackSpeed = 1;
  cropStart = -1;
  cropEnd = -1;
  playPosition = 1;
  isPlaying = false;
  max = 100;
  selectedJoint = '';
  features: any = {};
  featuresArray: Array<any> = [];
  selectedJointsForAngles: Array<string> = [];
  selectedJointsForDistance: Array<string> = [];
  selectedJointForHeight = '';
  color = ['#f72585', '#4361ee', '#4cc9f0', '#b5179e', '#4895ef', '#7209b7'];
  displayedColumns = ['start', 'mid', 'end'];
  tabPosition = -1;
  jointDictionary = joint_dictionary as { [key: string]: string; };


  // event listeners
  @HostListener('window:resize', ['$event'])
  onWindowResize(   event: UIEvent): void {
    this.viewHeight = window.innerHeight - 70 - 48 - 48;
    const width = window.innerWidth;
    const height = window.innerHeight - 64;
    this.renderer.setSize(width * 2 / 3, height);
    this.camera.aspect = (width * 2 / 3) / height;
    this.camera.updateProjectionMatrix();
  }

  @HostListener('document:mousemove', ['$event'])
  onDocumentMouseMove(event: MouseEvent): void {
    const rect = this.renderer.domElement.getBoundingClientRect();
    this.mouse.x = ((event.clientX - rect.left) / (rect.right - rect.left)) * 2 - 1;
    this.mouse.y = - ((event.clientY - rect.top) / (rect.bottom - rect.top)) * 2 + 1;
  }

  @HostListener('document:touchend', ['$event'])
  onTouchEnd(event: TouchEvent): void {
    const rect = this.renderer.domElement.getBoundingClientRect();
    this.touch.x = ((event.changedTouches[0].clientX - rect.left) / (rect.right - rect.left)) * 2 - 1;
    this.touch.y = - ((event.changedTouches[0].clientY - rect.top) / (rect.bottom - rect.top)) * 2 + 1;
  }

  ngAfterViewInit(): void {
    this.camera.lookAt(this.scene.position);
    this.editorContainer.nativeElement.appendChild(this.renderer.domElement);
    // this.editorContainer.nativeElement.addEventListener('click', (event: MouseEvent) => { this.addJointToStage(this.selectedJoint); });
    this.renderer.setPixelRatio(this.pixelRatio);
    this.renderer.setSize(this.viewWidth * 2 / 3, this.viewHeight);
    this.animate();
  }

  animate(): void {
    window.requestAnimationFrame(() => this.animate());
    this.renderer.render(this.scene, this.camera);
    this.selectJoint();
  }

  resetScene(): void {
    this.playbackSpeed = this.coordinatesService.playbackSpeed;
    this.scene = new three.Scene();
    this.initScene();
  }

  ngOnInit(): void {
    this.playbackSpeed = this.coordinatesService.playbackSpeed;
    this.initScene();                                                                                         
  }

  initScene(): void {
    this.renderer.setClearColor(0xffffff);
    this.camera.position.set(20, 20, 20);
    this.orbitControls.enablePan = true;
    this.orbitControls.enableZoom = true;
    this.orbitControls.enableKeys = true;

    const axesHelper = new three.AxesHelper(5);
    this.scene.add(axesHelper);

    const ambientLight = new three.AmbientLight(0xffffff);
    ambientLight.intensity = 2;
    this.scene.add(ambientLight);
    this.initSkeleton();
  }

  initSkeleton(): void {
    // tslint:disable-next-line:forin no-non-null-assertion
    for (const point in this.coordinatesService.lastPose!) {
      const geometry = new three.SphereBufferGeometry(0.1, 32, 32);
      const material = new three.MeshBasicMaterial({ color: 0xafaab9 });
      const dot = new three.Mesh(geometry, material);
      dot.name = point.toString();
      this.dots.push({ index: point, dot });
      this.scene.add(dot);
    }

    // tslint:disable-next-line: forin
    for (const start in this.coordinatesService.connections) {
      // tslint:disable-next-line: forin
      for (const end of this.coordinatesService.connections[start]) {
        const material = new three.LineBasicMaterial({
          color: 0x3f51b5,
          linewidth: 4.0
        });
        const geometry = new three.BufferGeometry();
        const line = new three.Line(geometry, material);
        this.lines.push(line);
        this.dotsMapping.push([start, end]);
        this.scene.add(line);
      }
    }
    this.coordinatesService.play.subscribe(pose => {
      if (pose) {
        this.recordingPose = pose;
        this.renderSkeleton(5, 0, 0, 0);
      }
    });

    this.coordinatesService.playPosition.subscribe(pos => {
      this.playPosition = pos;
      this.max = this.coordinatesService.recording.length - 1;
    });
  }

  renderSkeleton(scalar: number, offsetX: number, offsetY: number, offsetZ: number): void {
    const pose = this.recordingPose;
    for (const dot of this.dots) {
      dot.dot.position.setX(pose[dot.index].x * scalar + offsetX);
      dot.dot.position.setY(pose[dot.index].y * scalar + offsetY);
      dot.dot.position.setZ(pose[dot.index].z * scalar + offsetZ);
    }

    // tslint:disable-next-line: forin
    for (const index in this.lines) {
      const mapping = this.dotsMapping[index];
      const line = this.lines[index];
      const x0 = pose[mapping[0]].x * scalar + offsetX;
      const x1 = pose[mapping[1]].x * scalar + offsetX;
      const y0 = pose[mapping[0]].y * scalar + offsetY;
      const y1 = pose[mapping[1]].y * scalar + offsetY;
      const z0 = pose[mapping[0]].z * scalar + offsetZ;
      const z1 = pose[mapping[1]].z * scalar + offsetZ;
      const start = new three.Vector3(x0, y0, z0);
      const end = new three.Vector3(x1, y1, z1);
      line.geometry.setFromPoints([start, end]);
      line.geometry.computeBoundingBox();
      line.geometry.computeBoundingSphere();
    }
  }

  play(): void {
    this.coordinatesService.playRecording();
  }

  stop(): void {
    this.coordinatesService.stopPlayingRecording();
  }

  playPause(): void {
    if (!this.isPlaying) {
      this.coordinatesService.playRecording();
      this.isPlaying = true;
    } else {
      this.coordinatesService.stopPlayingRecording();
      this.isPlaying = false;
    }
  }

  setSpeed($event: any): void {
    this.coordinatesService.setPlaybackSpeed($event);
    this.playbackSpeed = $event;
  }

  setPlayPosition($event: any): void {
    this.coordinatesService.setPlayPosition(Math.round($event.value));
  }

  selectJoint(): void {
    this.raycaster.setFromCamera(this.mouse, this.camera);
    const mouseIntersection = this.raycaster.intersectObjects(this.scene.children);
    let go = '';
    for (const intersected of mouseIntersection) {
      if (intersected.object instanceof three.Mesh) {
        go = intersected.object.name;
      }
    }
    if (go.length >= 1) {
      this.selectedJoint = go;
    } else {
      this.selectedJoint = '';
    }
  }



  onSave(): void {
    if(this.id) {
      const exercise = {
        name: this.exName,
        description: this.exDescription,
        repetitions: this.repetitions,
        recording: this.coordinatesService.recording,
        features: this.features
      }
      this.dataService.updateExercise(this.id, exercise);
      return;
    }

    if (this.exName === '') {
      this.snackBar.open('Exercise name not set', 'OK', { duration: 2000 });
    } else {
      this.coordinatesService.saveRecording(this.exName, this.exDescription, this.repetitions, this.features);
    }
  }

  setCropStart(): void {
    if (this.cropEnd === -1) {
      this.cropStart = this.coordinatesService.ri;
    } else if (this.cropEnd <= this.coordinatesService.ri) {
      this.snackBar.open('Start has to be set after recording end', 'OK', { duration: 2000 });
    } else {
      this.cropStart = this.coordinatesService.ri;
    }
  }

  setCropEnd(): void {
    if (this.cropStart === -1) {
      this.cropEnd = this.coordinatesService.ri;
    } else if (this.cropStart >= this.coordinatesService.ri) {
      this.snackBar.open('End was setted to be after recording start', 'OK', { duration: 2000 });
    } else {
      this.cropEnd = this.coordinatesService.ri;
    }
  }

  saveCropping(): void {
    const croppedRecording = this.coordinatesService.recording.slice(this.cropStart, this.cropEnd + 1);
    this.coordinatesService.ri = 1;
    this.coordinatesService.recording = croppedRecording;
    this.coordinatesService.playRecording();
    this.isPlaying = true;
  }
  
  deleteFeature(i: number): void {
    delete this.features[i];
    this.featuresArray = Object.values(this.features);
  }


  addAngleToFeatures(): void {
    if (this.selectedJointsForAngles.length < 3) {
      this.snackBar.open('You have to choose at least 3 joints for an angle.', '0K', {duration: 2000});
    } else {
      let ft = {
        type: 'angle',
        category: '',
        val: this.selectedJointsForAngles,
        priority: ''
      }
      let index = Object.keys(this.features).length;
      this.features[index] = ft;
      console.log(this.jointDictionary);
      console.log(this.jointDictionary[ft.val[1]]);
      this.featuresArray = Object.values(this.features);
      this.selectedJointsForAngles = [];
    }

  }

  addDistanceToFeatures(): void {
    if (this.selectedJointsForDistance.length < 2) {
      this.snackBar.open('You have to choose at least 2 joints for a distance.', '0K', {duration: 2000});
    } else {
      let ft = {
        type: 'distance',
        category: '',
        val: this.selectedJointsForDistance,
        priority: ''
      }
      let index = Object.keys(this.features).length;
      this.features[index] = ft;
      this.featuresArray = Object.values(this.features);
      this.selectedJointsForDistance = [];
    }
  }

  addHeightToFeatures(): void {
    if (!this.selectedJointForHeight) {
      this.snackBar.open('You have to choose at least one joint for a height', '0K', {duration: 2000});
    } else {
      let ft = {
        type: 'height',
        category: '',
        val: this.selectedJointForHeight,
        priority: ''
      }
      let index = Object.keys(this.features).length;
      this.features[index] = ft;
      this.featuresArray = Object.values(this.features);
      this.selectedJointForHeight = '';
    }
  }

  addJointToAngle(): void {
    console.log(this.selectedJoint);
    if (this.selectedJoint) {
      if (this.selectedJointsForAngles.length >= 3) {
        this.snackBar.open('You can only choose three joints for a 3D-angle.', 'OK', {duration: 2000});
      } else {
        this.selectedJointsForAngles.push(this.selectedJoint);
      }
      console.log(this.selectedJointsForAngles);
    }
  }

  private addJointToDistance(): void {
    if (this.selectedJoint) {
      if (this.selectedJointsForDistance.length >= 2) {
        this.snackBar.open('You can only choose two joints for a 3D-distance.', 'OK', {duration: 2000});
      } else {
        this.selectedJointsForDistance.push(this.selectedJoint);
      }
    }
  }

  private addJointToHeight(): void {
    if (this.selectedJoint) {
      if (this.selectedJointForHeight) {
        this.snackBar.open('An height feature is defined only by one joint.', 'OK', {duration: 2000});
      } else {
        this.selectedJointForHeight = this.selectedJoint;
      }
    }
  }

  handleTouchEvent(): void {
    console.log(this.tabPosition);
    switch (this.tabPosition) {
        case TabPosition.Angle:
          this.addJointToAngle();
          break;
        case TabPosition.Distance:
          this.addJointToDistance();
          break;
        case TabPosition.Height:
          this.addJointToHeight();
          break;
      }
  }

  translate(name: string): string {
    console.log(name, this.jointDictionary[name]);
    return this.jointDictionary[name];
  }

  highlight(index: number): void {
    const feature = this.features[index];
    switch (feature.type) {
      case 'angle':
        const angleJoints = this.scene.children.filter(child => feature.val.includes(child.name));
        // tslint:disable-next-line:forin no-shadowed-variable
        for (const joint of angleJoints) {
          (joint as three.Mesh).material = new three.MeshBasicMaterial({ color: 0xf72585});
        }
        break;
      case 'height':
        const joint = this.scene.children.filter(child => child.name === feature[1][0]);
        (joint[0] as three.Mesh).material = new three.MeshBasicMaterial({ color: 0xf72585});
        break;
      case 'distance':
        const distanceJoints = this.scene.children.filter(child => feature[1].includes(child.name));
        // tslint:disable-next-line:forin no-shadowed-variable
        for (const joint of distanceJoints) {
          (joint as three.Mesh).material = new three.MeshBasicMaterial({ color: 0xf72585});
        }
        break;
    }
  }

  getColorGradientArray(color1: string, color2: string, repetitions: number, fade: number): string[] {
    const stack = [];
    const rgb1 = this.hexToRgb(color1);
    const rgb2 = this.hexToRgb(color2);
    for (let i = 0; i < repetitions; i++) {
      for (let f = 1; f <= fade; f++) {
        const r = Math.round(rgb1.r + (f / fade ) * (rgb2.r - rgb1.r));
        const g = Math.round(rgb1.g + (f / fade ) * (rgb2.g - rgb1.g));
        const b = Math.round(rgb1.b + (f / fade ) * (rgb2.b - rgb1.b));
        stack.push(this.rgbToHex({r, g, b}));
      }
    }
    return stack;
  }

  rgbToHex(rgb: { [key: string]: number }): string {
    const r = rgb.r.toString(16).length === 1 ? '0' + rgb.r.toString(16) : rgb.r.toString(16);
    const g = rgb.g.toString(16).length === 1 ? '0' + rgb.g.toString(16) : rgb.g.toString(16);
    const b = rgb.b.toString(16).length === 1 ? '0' + rgb.b.toString(16) : rgb.b.toString(16);
    return '#' + r + g + b;
  }

  hexToRgb(hex: string): { [key: string]: number } {
    hex = hex.slice(1);
    // Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
    const bigint = parseInt(hex, 16);
    // tslint:disable-next-line:no-bitwise
    const r = (bigint >> 16) & 255;
    // tslint:disable-next-line:no-bitwise
    const g = (bigint >> 8) & 255;
    // tslint:disable-next-line:no-bitwise
    const b = bigint & 255;
    return {r, g, b};
  }

}
